import { h, defineComponent, getCurrentInstance, onMounted } from 'vue'
import store from '@/store'
import './style.scss'
import { Subtract16Regular, Dismiss20Regular, SquareMultiple16Regular } from '@vicons/fluent'
import { NButton, NIcon, NPopover } from 'naive-ui'

export default defineComponent({
  props: {
    title: {
      type: String
    },
    bgColor: {
      type: String
    }
  },
  setup(props, { slots }) {
    /**
     * 
     */
    let parentInstance
    onMounted(()=>{
      parentInstance = getCurrentInstance().parent.ctx.$el
    })
    /**
     * 
     */
    const windowSizeObj=[
      {
        class: 'item1',
        children: [
          {id: 'halfOnLeft'},{id: 'halfOnRight'}
        ]
      },{
        class: 'item2',
        children: [
          {id: 'moreOnLeft'},{id: 'moreOnRight'}
        ]
      },{
        class: 'item3',
        children: [
          {id: 'thirdOnLeft'},{id: 'thirdOnCenter'},{id: 'thirdOnRight'}
        ]
      },{
        class: 'item4',
        children: [
          {id: 'halfOnLeft', class: 'item4A'},{id: 'thirdOnTop'},{id: 'thirdOnBottom'}
        ]
      },{
        class: 'item5',
        children: [
          {id: 'quarterOnLeftTop'},{id: 'thirdOnTop'},{id: 'quarterOnLeftBottom'},{id: 'thirdOnBottom'}
        ]
      },{
        class: 'item6',
        children: [
          {id: 'moreThirdLeft'},{id: 'moreThirdCenter'},{id: 'moreThirdRight'}
        ]
      },
    ]

    // 
    const rightBtn = value => {
      if(value==='mini'){
        store.dispatch('changeAppStatus',{
          name: props.title,
          key: 'hidden',
          value: true
        })
        store.dispatch('changeAppStatus',{
          name: props.title,
          key: 'mini',
          value: true
        })
      }else if(value==='close'){
        store.dispatch('changeAppStatus',{
          name: props.title,
          key: 'open',
          value: false
        })
      }else{
        parentInstance.style.left = ''
        parentInstance.style.top = ''
        if(parentInstance.className === 'appContainer'){
          parentInstance.className = 'appContainer centerCenter'
        }else{
          parentInstance.className = 'appContainer'
        }
      }
    }

    // 
    const changeWinSize = className => {
      parentInstance.className = `appContainer ${className}`
    }
    /**
     * 
     */
    // 
    function createSecondStepSize(data){
      return data.map(item=>{
        return h('div',{
          class: item.class?item.class:'',
          id: item.id,
          onClick: ()=>changeWinSize(item.id)
        })
      })
    }
    // 
    function createWinSize(){
      return windowSizeObj.map(item=>{
        return h('div',{
          class: item.class
        },createSecondStepSize(item.children))
      })
    }
    /**
     *  
     */
    function createRight(){
      const ary = [{
        type: 'mini',
        icon: Subtract16Regular,
        title: ''
      },{
        type: 'win',
        icon: SquareMultiple16Regular,
        title: ''
      },{
        type: 'close',
        icon: Dismiss20Regular,
        title: ''
      }]
      const btn =  ary.map(item=>{
        const btn = h(NButton,{
          style: {
            width: '40px',
            height: '40px',
            fontSize: '18px'
          },
          class: {
            closeBtn: item.type==='close',
            otherBtn: item.type !== 'close'
          },
          size: 'tiny',
          title: item.title,
          bordered: false,
          onClick: event=>{
            event.stopPropagation()
            rightBtn(item.type)
          },
          text: true
        },()=>h(NIcon,null,()=>h(item.icon)))

        if(item.type==='win'){
          return h(NPopover,{
            trigger: 'hover',
            class: 'myPopover',
            style: {
              padding: '8px'
            },
            'show-arrow': false
          },{
            trigger: ()=>btn,
            default: ()=>h('div',{
              class: 'winBox'
            },createWinSize())
          })
        }
        return btn
      })
      return h('div',{
        class: 'btnBox'
      },btn)
    }

    /**
     * 
     */
    function createLeft(){
      if(slots.default){
        return slots.default()
      }
      // 
      return h('div',{
        class: 'titleBlockLeft'
      },props.title)
    }

    return () =>
      h('div',{
          style: {
            backgroundColor: props.bgColor?props.bgColor:'--global-bg-color'
          },
          class: 'titleBlock',
          onDblclick: ()=>rightBtn()
        },[
          createLeft(),
          createRight()
        ]
      )
  }
})